
import { ImageAsset, Node, Sprite, SpriteFrame, Toggle, _decorator, instantiate } from 'cc';
import { GCache } from '../../cache/GCache';
import { AppEvent } from '../../config/AppEvent';
import { AppSound } from '../../config/AppSound';
import { GameRes } from '../../config/GameRes';
import { Utils } from '../../framework/Utils';
import { resLoader } from '../../framework/loader/ResLoader';
import { EventManager } from '../../framework/manager/EventManager';
import { BaseComponent } from '../../framework/vc/BaseComponent';
const { ccclass, property } = _decorator;

/**
 * Name = PictureTabDialogComponent
 * URL = db://assets/package/login/scripts/PictureTabDialogComponent.ts
 * Time = Thu Aug 04 2022 17:12:23 GMT+0800 (中国标准时间)
 * Author = xueya
 *
 * Life: onLoad-->onEnable->start->update->lateUpdate->onDisable->onDestroy
 * 
 * 用户协议和隐私政策
 */

@ccclass('PictureTabDialogComponent')
export class PictureTabDialogComponent extends BaseComponent {

	//正文标题
	@property({ tooltip: "标题内容", type: Node })
	titleNode: Node = null!;

	//正文节点
	@property({ tooltip: "正文节点", type: Node })
	contentNode: Node = null!;

	//滚动条节点
	@property({ tooltip: "滚动条节点", type: Node })
	scrollowBarNode: Node = null!;

	//菜单模板
	@property({ tooltip: "菜单列表节点", type: Node })
	menuListNode: Node = null!;
	
	//菜单模板
	@property({ tooltip: "菜单模板", type: Node })
	menuTemplate: Node = null!;

	//无内容时的节点
	@property({ tooltip: "无内容时的节点", type: Node })
	nodeNoTips: Node = null!;

	private _allResMapList = {
		"玩法规则": GameRes.Picture_GameRule,
	}

	private _loadResMapList: Map<string, Map<number, Map<string, Map<number, ImageAsset>>>> = new Map();
	//当前最大资源加载数量
	private _curMaxResNum: Map<string, number> = new Map();
	//当前资源加载数量
	private _curResNum: Map<string, number> = new Map();
	//当前类型
	private _dialogType: string = null;
	//当前选择tab
	private _curSeletedTab: number = 0;

	constructor() {
		super();
		this.preLoad();
	}
	//预加载
	preLoad() {
		if (!GCache.firstRunLoadSuccess) {
			return;
		}
		let self = this;
		for (let type in this._allResMapList) {
			self._loadResMapList.set(type, new Map());
			self._curMaxResNum.set(type, 0);
			self._curResNum.set(type, 0);
			
			let resConf = self._allResMapList[type];
			for (let i = 0; i < resConf.length; i++) {
				self._loadResMapList.get(type).set(i, new Map());
				self._loadResMapList.get(type).get(i).set("tab", new Map());
				self._loadResMapList.get(type).get(i).set("content", new Map());
				
				let oneResConf = resConf[i];
				for (let j = 0; j < oneResConf.tab.length; j++) {
					let conf = oneResConf.tab[j];
					self._curMaxResNum.set(type, self._curMaxResNum.get(type) + 1);
					resLoader.load(conf.bundle, conf.path, ImageAsset, (err: Error | null, imageAsset) => {
						if (err || !self._loadResMapList) {
							self.print("error", err)
							return;
						}
						self._loadResMapList.get(type).get(i).get("tab").set(j, imageAsset);
		
						self._curResNum.set(type, self._curResNum.get(type) + 1);

						if (self._dialogType == type) {
							self.updateView();
						}
					});
				}
				for (let j = 0; j < oneResConf.content.length; j++) {
					let conf = oneResConf.content[j];
					self._curMaxResNum.set(type, self._curMaxResNum.get(type) + 1);
					resLoader.load(conf.bundle, conf.path, ImageAsset, (err: Error | null, imageAsset) => {
						if (err || !self._loadResMapList) {
							self.print("error", err)
							return;
						}
 						self._loadResMapList.get(type).get(i).get("content").set(j, imageAsset);


						self._curResNum.set(type, self._curResNum.get(type) + 1);

						if (self._dialogType == type) {
							self.updateView();
						}
					});
				}
			};
		}
	}
	onLoad() {
		this.initData();
		this.initView();
		this.updateView();
	};
	start() {
	}

	initData() {
		let comp = null;
		comp = this.node.getComponent("DelegateComponent")
		if (comp) {
			this._dialogType = comp.getParams()
			this._curSeletedTab = 0;
		}
	}

	initView() {
	}
	
	/** 更新界面 */
	updateView() {
		if (Utils.isNull(this._dialogType)) {
			this.print("PictureTabDialogComponent", "updateView", "参数错误");
			return;
		}

		if (Utils.isNull(this._allResMapList[this._dialogType])) {
			this.print("PictureTabDialogComponent", "updateView", "弹框类型错误");
			return;
		}

		if (this._curResNum.get(this._dialogType) != this._curMaxResNum.get(this._dialogType)) {
			this.print("PictureTabDialogComponent", "updateView", "资源加载中");
			return;
		}
		
		let resLoad = this._loadResMapList.get(this._dialogType)
		if (this.menuListNode.children.length <= 1) {
			for (let i = 0; i < resLoad.size; i++) {
				let oneResLoad = resLoad.get(i);
				if (Utils.isNotNull(oneResLoad.get("tab"))) {
					let menuNode = instantiate(this.menuTemplate);
					let toggle = menuNode.getComponent(Toggle)
					toggle.clickEvents[0].customEventData = String(i);
					menuNode.getComponent(Sprite).spriteFrame = Utils.image_ImageAssetToSpriteFrame(oneResLoad.get("tab").get(0));
					
					let checkmark: Node = menuNode.getChildByName("Checkmark");
					checkmark.getComponent(Sprite).spriteFrame = Utils.image_ImageAssetToSpriteFrame(oneResLoad.get("tab").get(1));
					menuNode.active = true;
					this.menuListNode.addChild(menuNode);

					if (this._curSeletedTab == i) {
						toggle.isChecked = true;
						this.updateViewContent();
					}
				}
			}
		}
	}

	onClickItem(event: Event, customEventData: string){
		this._curSeletedTab = Utils.number_valueOf(customEventData, 0)
		this.updateViewContent();
    }

	/** 更新内容 */
	updateViewContent() {
		if (Utils.isNull(this._dialogType)) {
			this.print("PictureTabDialogComponent", "updateView", "参数错误");
			return;
		}

		if (Utils.isNull(this._allResMapList[this._dialogType])) {
			this.print("PictureTabDialogComponent", "updateView", "弹框类型错误");
			return;
		}

		let resLoad = this._loadResMapList.get(this._dialogType);
		let resSelected = resLoad.get(this._curSeletedTab);
		if (Utils.isNull(resSelected)) {
			this.print("PictureTabDialogComponent", "updateView", "tab索引错误");
			return;
		}

		if (Utils.isNotNull(resSelected.get("content"))) {
			this.contentNode.removeAllChildren();
			this.nodeNoTips.active = false;
			for (let i = 0; i < resSelected.get("content").size; i++) {
				let imageAssert = resSelected.get("content").get(i);
				if (imageAssert) {
					let spriteFrame = Utils.image_ImageAssetToSpriteFrame(imageAssert);
					if (spriteFrame) {
						this.contentNode.addChild(this.creatorSpriteNode(spriteFrame));
					}
				}
			}
		}
	}

	//点击了取消/关闭
	onClickCancel() {
		EventManager.dispatch(AppEvent.SYS_PLAY_EFFECT, AppSound.CommClick);
		this.node.destroy();
	}

	creatorSpriteNode(spriteFrame: SpriteFrame): Node {
		let node = Utils.create_2DSprite(spriteFrame, "Sprite");
		return node;
	}

	//销毁
	onDestroy() {

	};

}

